* {
    box-sizing: border-box;
}

html,
body,
#root {
    --max-desktop-width: 1100px;
    height: 100%;
}

body {
    overflow: hidden;
    margin: 0;
    font-family: -apple-system, 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.root-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;

    .main-wrapper {
        position: relative;
        margin: 0 auto;
        flex: 1 1 auto;
        overflow-y: auto;
        width: 100%;
        max-width: var(--max-desktop-width);
    }
}

input, select {
    border-style: solid;
    border-width: 1px;
    border-color: #787878;
    border-radius: 4px;
    height: 40px;
    padding: 0.5rem;

    &:focus-visible {
        outline: none;
    }
}